html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html,
body {
  height: 100%;
  font-family: Arial, Helvetica, sans-serif; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400; }

form {
  margin: 0; }

fieldset {
  padding: 0;
  border: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: inherit; }

address {
  font-style: inherit; }

p {
  margin: 0; }

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer; }

input {
  border: inherit;
  padding: 0;
  margin: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.content {
  flex: 1 0 auto; }

.footer {
  flex: 0 0 auto; }

.container-main,
.container-minor {
  width: 100%;
  margin: 0 auto; }

.container-main {
  max-width: 1280px;
  padding: 0 40px 0; }

.container-minor {
  max-width: 1031px;
  padding: 0 20px 0; }

.visually-hidden {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.reset-button {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer; }
  .reset-button:disabled {
    cursor: default; }

.title {
  font-weight: bold; }

input::-webkit-search-decoration,
input::-webkit-search-cancel-button,
input::-webkit-search-results-button,
input::-webkit-search-results-decoration {
  display: none; }

.header {
  background-color: #fff;
  height: 75px;
  border-bottom: 1px solid #cf0a2c; }
  .header__container {
    display: flex;
    align-items: center;
    align-content: stretch;
    flex-wrap: nowrap;
    height: 100%;
    position: relative;
    z-index: 1; }
  .header__logo {
    height: 29px; }
  .header__logo-image {
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center; }
  .header__search-btn {
    margin-left: 106px;
    width: 20px;
    height: 20px;
    margin-top: 6px;
    transform: scale(1); }
    .header__search-btn.inactive {
      transform: scale(0);
      overflow: hidden; }
  .header__search-btn-image {
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
    width: 100%;
    height: 100%; }

.menu {
  margin-left: auto;
  font-size: 14px;
  color: #333;
  display: flex;
  height: 100%;
  padding-top: 33px;
  gap: 0 53px;
  transition: 0.3s all; }
  .menu.inactive {
    transform: scale(0);
    overflow: hidden; }
  .menu__item {
    position: relative;
    z-index: 5;
    cursor: pointer; }
    .menu__item::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: #cf0a2c;
      z-index: 5;
      transition: 0.3s all;
      opacity: 0; }
    .menu__item:hover::after {
      opacity: 1; }
  .menu__item-title {
    color: #000; }
  .menu__dropdown {
    position: fixed;
    z-index: 3;
    left: 0;
    top: 73px;
    margin-top: -1px;
    padding: 0;
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid #d4d4d4;
    display: flex;
    flex-direction: column;
    cursor: default; }
  .menu__dropdown-top {
    display: flex;
    align-items: center;
    min-height: 214px;
    height: auto;
    background-color: #fff; }
  .menu__dropdown-top-list {
    display: flex;
    gap: 0 15px; }
  .menu__dropdown-top-item {
    flex: 1 1 20%; }
  .menu__dropdown-top-link {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80px; }
    .menu__dropdown-top-link img {
      display: inline-block;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat; }
    .menu__dropdown-top-link span {
      display: inline-block;
      margin-top: 12px;
      color: #595757;
      text-align: center; }
    .menu__dropdown-top-link:hover span {
      -webkit-text-decoration-line: underline;
              text-decoration-line: underline;
      color: #333333; }
  .menu__dropdown-bottom {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 91px;
    background-color: #f7f7f7; }
  .menu__dropdown-bottom-link {
    position: relative;
    height: 40px;
    padding-left: 56px;
    padding-right: 35px;
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    color: #595757; }
    .menu__dropdown-bottom-link::after {
      content: "";
      position: absolute;
      right: 0;
      top: 10px;
      background-image: url(../images/link-vector.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      width: 18px;
      height: 18px;
      transition: 0.3s all;
      opacity: 0; }
    .menu__dropdown-bottom-link::before {
      content: "";
      position: absolute;
      left: 0;
      top: 9px;
      background-image: url(../images/consumers.svg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      width: 40px;
      height: 24px; }
    .menu__dropdown-bottom-link:hover {
      color: #333333; }
      .menu__dropdown-bottom-link:hover::after {
        opacity: 1; }
  .menu__dropdown-close {
    display: inline-block;
    width: 20px;
    height: 20px;
    position: absolute;
    z-index: 4;
    right: 20%;
    bottom: 35px; }

.lang {
  margin-left: auto;
  text-transform: uppercase;
  font-size: 14px;
  color: #333;
  padding-top: 10px; }
  .lang.inactive {
    transform: scale(0);
    overflow: hidden; }
  .lang__button {
    padding-right: 20px;
    position: relative; }
    .lang__button::after {
      content: "";
      position: absolute;
      top: 4px;
      right: 0;
      background-image: url("../images/arrow-down.svg");
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      width: 10px;
      height: 6px;
      transition: 0.3s all; }
    .lang__button.active::after {
      transform: rotateZ(180deg); }
  .lang__dropdown {
    transition: 0.3s all;
    position: absolute;
    padding-top: 10px;
    max-height: 0px;
    overflow: hidden; }
  .lang__form {
    display: flex;
    flex-direction: column; }
  .lang__form-label {
    cursor: pointer; }
  .lang__form-input {
    display: none; }

.search {
  display: flex;
  position: absolute;
  right: 42px;
  align-items: center;
  transform: scale(0);
  transition: 0.3s all; }
  .search.active {
    visibility: visible;
    transform: scale(1);
    z-index: 2; }
  .search__label {
    display: flex;
    max-width: 350px;
    position: relative; }
    .search__label::after {
      content: "";
      position: absolute;
      right: 15px;
      bottom: calc(50% - 10px);
      background-image: url(../images/searcher.svg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      width: 22px;
      height: 20px; }
  .search__input {
    padding: 10px 44px 10px 20px;
    display: block;
    height: 50px;
    outline: none;
    width: 350px;
    background: #ffffff;
    border: 1px solid #bebdbd;
    transition: 0.3s all; }
    .search__input::-moz-placeholder {
      font-size: 16px;
      line-height: 18px;
      color: #757575; }
    .search__input::placeholder {
      font-size: 16px;
      line-height: 18px;
      color: #757575; }
    .search__input:focus {
      border: 1px solid #bebdbd;
      color: #282828;
      box-shadow: 0px 3px 8px #d4d4d4; }
  .search__close {
    width: 20px;
    height: 20px;
    margin-left: 10px; }
  .search__close-image {
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: center;
       object-position: center;
    width: 100%;
    height: 100%; }
